// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'

function changeComplimentaryAccessRequiring(required: boolean) {
  const complimentaryAccessTextarea = document.getElementById(`copilot-complimentary-access-textarea`)!
  const complimentaryAccessDurationUnlimited = document.getElementById(`complimentary_access_duration_unlimited`)!
  const complimentaryAccessDurationSpecified = document.getElementById(`complimentary_access_duration_specified`)!
  const complimentaryAccessDurationMonth = document.getElementById(`complimentary_access_duration_month`)!
  const complimentaryAccessDurationDay = document.getElementById(`complimentary_access_duration_day`)!
  const complimentaryAccessDurationYear = document.getElementById(`complimentary_access_duration_year`)!

  if (required) {
    complimentaryAccessTextarea.setAttribute('required', 'required')
    complimentaryAccessDurationUnlimited.setAttribute('required', 'required')
    complimentaryAccessDurationSpecified.setAttribute('required', 'required')
    complimentaryAccessDurationMonth.setAttribute('required', 'required')
    complimentaryAccessDurationDay.setAttribute('required', 'required')
    complimentaryAccessDurationYear.setAttribute('required', 'required')
  } else {
    complimentaryAccessTextarea.removeAttribute('required')
    complimentaryAccessDurationUnlimited.removeAttribute('required')
    complimentaryAccessDurationSpecified.removeAttribute('required')
    complimentaryAccessDurationMonth.removeAttribute('required')
    complimentaryAccessDurationDay.removeAttribute('required')
    complimentaryAccessDurationYear.removeAttribute('required')
  }
}

on('click', '.js-free-access-reason', function (event) {
  const button = event.currentTarget as HTMLInputElement
  const selectedReason = button.value

  const technicalPreviewExtensionDetails = document.getElementById(`technical_preview_extension_details`)!
  const yCombinatorDetails = document.getElementById(`y_combinator_details`)!
  const complimentaryAccessDetails = document.getElementById(`complimentary_access_details`)!

  if (selectedReason === 'complimentary_access') {
    complimentaryAccessDetails.removeAttribute('hidden')
    changeComplimentaryAccessRequiring(true)
  } else {
    complimentaryAccessDetails.setAttribute('hidden', 'true')
    changeComplimentaryAccessRequiring(false)
  }

  if (selectedReason === 'y_combinator') {
    yCombinatorDetails.removeAttribute('hidden')
  } else {
    yCombinatorDetails.setAttribute('hidden', 'true')
  }

  if (selectedReason === 'technical_preview_extension') {
    technicalPreviewExtensionDetails.removeAttribute('hidden')
  } else {
    technicalPreviewExtensionDetails.setAttribute('hidden', 'true')
  }
})

on('click', '.js-complimentary-access-duration', function (event) {
  const button = event.currentTarget as HTMLInputElement
  const selectedDuration = button.value

  const complimentaryAccessDurationMonth = document.getElementById(`complimentary_access_duration_month`)!
  const complimentaryAccessDurationDay = document.getElementById(`complimentary_access_duration_day`)!
  const complimentaryAccessDurationYear = document.getElementById(`complimentary_access_duration_year`)!

  if (selectedDuration === 'specified') {
    complimentaryAccessDurationMonth.setAttribute('required', 'required')
    complimentaryAccessDurationMonth.removeAttribute('disabled')
    complimentaryAccessDurationDay.setAttribute('required', 'required')
    complimentaryAccessDurationDay.removeAttribute('disabled')
    complimentaryAccessDurationYear.setAttribute('required', 'required')
    complimentaryAccessDurationYear.removeAttribute('disabled')
  } else if (selectedDuration === 'unlimited') {
    complimentaryAccessDurationMonth.setAttribute('disabled', 'disabled')
    complimentaryAccessDurationMonth.removeAttribute('required')
    complimentaryAccessDurationDay.setAttribute('disabled', 'disabled')
    complimentaryAccessDurationDay.removeAttribute('required')
    complimentaryAccessDurationYear.setAttribute('disabled', 'disabled')
    complimentaryAccessDurationYear.removeAttribute('required')
  }
})
