import {fromEvent} from '@github-ui/subscription'
// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'

observe('.js-bulk-takedown-status-channel', {
  subscribe: el =>
    fromEvent(el, 'socket:message', function (event: Event) {
      const currentTarget = event.currentTarget as Element
      const data = (event as CustomEvent).detail.data
      for (const key in data) {
        const status = currentTarget.querySelector(`.js-disable-repository-access-status[data-repo-id='${key}']`)
        if (status) status.textContent = data[key]['user_display_status']
      }
    }),
})
