import {fetchSafeDocumentFragment} from '@github-ui/fetch-utils'

// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'

observe('#js-repair-index', {
  initialize(el) {
    setInterval(async function refreshRepairStats() {
      const indexName = el.getAttribute('data-index-name')!
      const repairStatus = await fetchSafeDocumentFragment(document, `/stafftools/indexing/${indexName}/repair`)
      el.textContent = ''
      el.append(repairStatus)
    }, 1000 * 5)
  },
})
