// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'
import {onFocus} from '@github-ui/onfocus'

onFocus('.js-business-tos-company-name', function (el) {
  if (el instanceof HTMLInputElement && el.value === '') {
    const nameInput = document.querySelector<HTMLInputElement>('.js-business-name')!
    el.value = nameInput.value
  }
})

on('click', '#staff-admin-complete-results .member-suggestion-info', function ({currentTarget}) {
  const form = currentTarget.closest<HTMLElement>('form')!
  const addText = form.querySelector<HTMLElement>('.js-add-btn')!
  const inviteText = form.querySelector<HTMLElement>('.js-invite-btn')!

  const li = currentTarget.closest<HTMLElement>('li')!
  const isEmail = li.classList.contains('email-suggestion')

  addText.hidden = isEmail
  inviteText.hidden = !isEmail
})

on('click', '.js-submit-emubusinesstype', function () {
  // get the radio button that is selected for business[seats_plan_type]
  const selected = document.querySelector<HTMLInputElement>('input[name="business[seats_plan_type]"]:checked')
  const copilotMaxSeats = document.getElementsByName('copilot_max_seats')[0] as HTMLDataListElement
  if (selected && selected.value === 'basic') {
    // we need to show the DL copilot_max_seats
    copilotMaxSeats.removeAttribute('style')
  } else {
    copilotMaxSeats.setAttribute('style', 'display:none;')
  }
})

on('click', '.js-submit-emucheckbox', function (event) {
  const target = event.currentTarget as HTMLInputElement
  const shortcodeDl = document.getElementsByName('shortcode')[0] as HTMLDataListElement
  const input = document.getElementsByName('business[shortcode]')[0] as HTMLInputElement
  const seatsPlanType = document.getElementsByName('seats_plan_type')[0] as HTMLDataListElement
  if (target !== null && input !== null && shortcodeDl !== null && target.checked) {
    shortcodeDl.removeAttribute('style')
    seatsPlanType.removeAttribute('style')
    input.removeAttribute('disabled')
    input.focus()
  } else if (target !== null && input !== null && shortcodeDl !== null && !target.checked) {
    shortcodeDl.setAttribute('style', 'display:none;')
    if (!seatsPlanType.hasAttribute('data-create-basic')) {
      seatsPlanType.setAttribute('style', 'display:none;')
    }
    input.setAttribute('disabled', 'true')
    input.value = ''
  }
})
