import {controller, target} from '@github/catalyst'
import {html, render} from 'lit-html'
import {fetchPoll} from '@github-ui/fetch-utils'

type JobResponse = Response & {
  job_url: string
}

@controller
class PollSynchronousPaymentElement extends HTMLElement {
  @target buttonCTA: HTMLElement
  @target loadingStatus: HTMLElement
  @target completionMessage: HTMLElement
  @target buttonElement: HTMLButtonElement
  @target form: HTMLFormElement

  connectedCallback() {
    this.loadingStatus.style.display = 'none'
  }

  async collectPayment(e: Event) {
    this.buttonElement.disabled = true
    this.loadingStatus.style.display = ''
    e.preventDefault()

    try {
      const job_url = await this.fetchPaymentCollectionJobUrl()
      if (!job_url) return this.refreshPage()

      const job_status = await this.pollPaymentCollectionJobStatus(job_url)
      this.loadingStatus.style.display = 'none'

      if (job_status.state === 'success') {
        const successHTML = html`<p style="text-align:center">
          Success! <a class="Link--inTextBlock" href="" id="refresh-page-anchor">Refresh the page to see changes</a>
        </p>`
        render(successHTML, this.completionMessage)
        document.getElementById('refresh-page-anchor')?.addEventListener('click', () => this.refreshPage())
      } else {
        const failedHTML = html`<p style="text-align:center">Error: ${job_status.error_message}</p>`
        render(failedHTML, this.completionMessage)
      }
    } catch {
      const errorHTML = html`<p style="text-align:center">
        An error occurred while waiting for a response. The change may or may not have been successful, please see the
        audit log for details.
      </p>`
      render(errorHTML, this.completionMessage)
    }
  }

  async pollPaymentCollectionJobStatus(url: string) {
    const response = await fetchPoll(url, {headers: {accept: 'application/json'}}, 1000, [200, 500])
    const responseData = await response.json()
    return responseData.job
  }

  async fetchPaymentCollectionJobUrl() {
    const response = await fetch(this.form.action, {
      method: this.form.method,
      body: new FormData(this.form),
      headers: {
        Accept: 'application/json',
        'X-Requested-With': 'XMLHttpRequest',
      },
    })
    const responseData: JobResponse = await response.json()
    return responseData.job_url
  }

  refreshPage() {
    window.location.reload()
  }
}
