import {remoteForm} from '@github/remote-form'

remoteForm('.js-action-ldap-create-form', async function (form, wants) {
  form.querySelector<HTMLElement>('.btn-sm')!.classList.add('disabled')

  let msg
  let success = false
  try {
    const response = await wants.text()
    msg = response.text
    success = true
  } catch (error) {
    msg = 'Oops, something went wrong.'
  }

  const container = form.closest<HTMLElement>('.js-action-ldap-create')!
  const message = container.querySelector<HTMLElement>('.js-message')!
  message.style.display = 'block'
  message.innerHTML = ` &ndash; ${msg}`

  if (success) {
    form.querySelector<HTMLElement>('.btn')!.style.display = 'hidden'
  }
})
