// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'

// Handle select changed
on('change', '#js-ghas-state-select', function (event) {
  const seatsFormGroup = document.getElementById('js-ghas-seats-form-group')
  const seatsInput = document.getElementById('js-ghas-seats-input')
  if (seatsFormGroup && seatsInput) {
    // This value name needs to match one of the Stafftools::AdvancedSecurityHelper constants
    const seatsSelected = (event.currentTarget as HTMLSelectElement).value === 'seats'
    seatsFormGroup.style.display = seatsSelected ? 'block' : 'none'
    const seatsInputElement = seatsInput as HTMLInputElement
    seatsInputElement.min = seatsSelected ? '1' : '0'
  }
})
