// eslint-disable-next-line no-restricted-imports
import {fire, on} from 'delegated-events'
import AutocompleteElement from '@github/auto-complete-element'
// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'
import {remoteForm} from '@github/remote-form'
import {requestSubmit} from '@github-ui/form-utils'

function sponsorsBulkManageButtonClicked(clickedButton: EventTarget & Element, formSelector: string) {
  const container = clickedButton.closest('.js-sponsors-bulk-manage-listings-container')!
  const form = container.querySelector<HTMLFormElement>(formSelector)!
  const listingsTable = container.querySelector('.js-sponsors-bulk-manage-listings-table')!
  const bulkManageButtons = container.querySelectorAll<HTMLButtonElement>('.js-sponsors-bulk-manage-listings-button')
  const otherBulkManageButtons = Array.from(bulkManageButtons).filter(button => button !== clickedButton)

  form.hidden = !form.hidden
  clickedButton.classList.toggle('selected')
  listingsTable.classList.toggle('on')

  const shouldDisableOthers = clickedButton.classList.contains('selected')
  for (const otherButton of otherBulkManageButtons) {
    otherButton.disabled = shouldDisableOthers
  }
}

function sponsorsRetryOneTimeSubscriptionItemsButtonClicked(
  clickedButton: EventTarget & HTMLElement,
  formSelector: string,
) {
  const form = document.querySelector<HTMLFormElement>(formSelector)
  if (!form) return

  clickedButton.hidden = true

  const subscriptionItemsTable = form.querySelector<HTMLTableElement>(
    '.js-sponsors-retry-one-time-subscription-items-table',
  )
  if (subscriptionItemsTable) subscriptionItemsTable.classList.toggle('on')
}

function toggleSponsorsBulkActionCheckbox(tableRow: HTMLElement, tableSelector: string) {
  const table = tableRow.closest(tableSelector)
  if (!table || !table.classList.contains('on')) return

  const checkbox = tableRow.querySelector<HTMLInputElement>('.js-sponsors-bulk-action-checkbox')
  if (checkbox) checkbox.checked = !checkbox.checked
}

on('click', '.js-sponsors-bulk-ban-listings-button', event => {
  const button = event.currentTarget
  sponsorsBulkManageButtonClicked(button, '.js-sponsors-bulk-ban-listings-form')
})

on('click', '.js-sponsors-retry-one-time-subscription-items-button', event => {
  const button = event.currentTarget as HTMLElement
  sponsorsRetryOneTimeSubscriptionItemsButtonClicked(button, '.js-sponsors-retry-one-time-subscription-items-form')
})

on('click', '.js-sponsors-bulk-approve-listings-button', event => {
  const button = event.currentTarget
  sponsorsBulkManageButtonClicked(button, '.js-sponsors-bulk-approve-listings-form')
})

function removeSponsorsMaintainerTag(tagInput: HTMLInputElement) {
  const tagInputListItem = tagInput.closest<HTMLElement>('.js-tag-input-tag')!
  const tagInputForm = tagInput.form!

  tagInputListItem.remove()
  fire(tagInputForm, 'tags:changed')
}

function normalizeSponsorableLogin(login: string) {
  return login.toLowerCase().trim()
}

function findSponsorsMaintainerTagForCheckbox(checkbox: HTMLInputElement) {
  const inputName = checkbox.name
  const sponsorableLogin = normalizeSponsorableLogin(checkbox.value)
  const container = checkbox.closest('.js-sponsors-bulk-manage-listings-container')!
  const activeForm = container.querySelector('.js-sponsors-bulk-manage-listings-form:not([hidden])')!
  const tagInputContainer = activeForm.querySelector('.js-sponsors-bulk-manage-listings-tag-input')!
  const inputSelector = `input[name='${inputName}'][value='${sponsorableLogin}']`
  return tagInputContainer.querySelector<HTMLInputElement>(inputSelector)
}

on('change', '.js-sponsors-bulk-action-checkbox', event => {
  const checkbox = event.currentTarget as HTMLInputElement
  const container = checkbox.closest('.js-sponsors-bulk-manage-listings-container')
  if (!container) return

  const activeForm = container.querySelector('.js-sponsors-bulk-manage-listings-form:not([hidden])')
  if (!activeForm) return

  const autocomplete = activeForm.querySelector<AutocompleteElement>('.js-sponsors-bulk-manage-listings-autocomplete')!
  const sponsorableLogin = normalizeSponsorableLogin(checkbox.value)

  if (checkbox.checked) {
    autocomplete.value = sponsorableLogin
  } else {
    autocomplete.value = ''
    const tagInput = findSponsorsMaintainerTagForCheckbox(checkbox)
    if (tagInput) removeSponsorsMaintainerTag(tagInput)
  }
})

function findSponsorsMaintainerCheckboxForTag(tagInputListItem: HTMLElement) {
  const hiddenInput = tagInputListItem.querySelector<HTMLInputElement>('input[type="hidden"]')!
  const inputName = hiddenInput.name
  const sponsorableLogin = hiddenInput.value
  const container = document.querySelector('.js-sponsors-bulk-manage-listings-container')!
  const listingsTable = container.querySelector('.js-sponsors-bulk-manage-listings-table')!
  const checkboxSelector = `input.js-sponsors-bulk-action-checkbox[name='${inputName}'][value='${sponsorableLogin}']`
  return listingsTable.querySelector<HTMLInputElement>(checkboxSelector)
}

on('click', '.js-sponsors-bulk-manage-listings-remove-tag', event => {
  const removeButton = event.currentTarget
  const tagInputListItem = removeButton.closest<HTMLElement>('.js-tag-input-tag')!
  const checkbox = findSponsorsMaintainerCheckboxForTag(tagInputListItem)
  if (checkbox) {
    checkbox.checked = false
  }
})

function findSponsorsMaintainerCheckboxForLogin(sponsorableLogin: string) {
  const listingsTable = document.querySelector('.js-sponsors-bulk-manage-listings-table')!
  const inputName = 'sponsorable_logins[]'
  const checkboxSelector = `input.js-sponsors-bulk-action-checkbox[name='${inputName}'][value='${sponsorableLogin}']`
  return listingsTable.querySelector<HTMLInputElement>(checkboxSelector)
}

observe('.js-sponsors-bulk-manage-listings-autocomplete', {
  add(autocomplete) {
    if (!(autocomplete instanceof AutocompleteElement)) return

    autocomplete.addEventListener('auto-complete-change', function () {
      const sponsorableLogin = autocomplete.value
      if (sponsorableLogin.length < 1) return

      const checkbox = findSponsorsMaintainerCheckboxForLogin(sponsorableLogin)
      if (checkbox) {
        checkbox.checked = true
      }
    })
  },
})

function showPotentialSponsorableBannerPreview(fieldName: string, fieldValue: string) {
  const previewForm = document.querySelector<HTMLFormElement>('.js-potential-sponsorship-banner-preview-form')
  if (!previewForm) return

  const input = previewForm.querySelector<HTMLInputElement>(`input[name='${fieldName}']`)
  if (!input) return

  input.value = fieldValue
  requestSubmit(previewForm)
}

on('change', '.js-potential-sponsorship-form textarea', event => {
  const textarea = event.currentTarget as HTMLTextAreaElement
  showPotentialSponsorableBannerPreview(textarea.name, textarea.value)
})

remoteForm('.js-potential-sponsorship-banner-preview-form', async function (form, wants) {
  try {
    const response = await wants.html()
    const bannerPreviewHTML = response.html
    form.textContent = ''
    form.appendChild(bannerPreviewHTML)
    form.hidden = false
  } catch (responseError) {
    form.hidden = true
  }
})

on('click', '.js-sponsors-bulk-action-row', event => {
  const clickedElement = event.target as HTMLElement
  if (clickedElement.closest('a') || clickedElement.tagName === 'INPUT') return

  const row = clickedElement.closest<HTMLElement>('.js-sponsors-bulk-action-row')!
  toggleSponsorsBulkActionCheckbox(row, '.js-sponsors-retry-one-time-subscription-items-table')
})
