// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'

// Modify warning message based on warning type
function toggleMailerWarning(canApprove: boolean) {
  const warning = document.querySelector<HTMLElement>('#two-factor-approval-warning')!
  const emailWarning = document.querySelector<HTMLElement>('#two-factor-approval-email-warning')!
  if (canApprove) {
    warning.hidden = false
    emailWarning.hidden = true
  } else {
    emailWarning.hidden = false
    warning.hidden = true
  }
}

// Disable submission unless at least one email is selected
function toggleApprovalButton(canApprove: boolean) {
  const button = document.querySelector<HTMLElement>('#two-factor-approve-btn')!
  if (canApprove) {
    button.classList.remove('disabled')
  } else {
    button.classList.add('disabled')
  }
}

function checkedInputsCount(el: HTMLElement) {
  return el.querySelectorAll('input[type=checkbox]:checked').length
}

function requireMailer(el: HTMLElement) {
  const canApprove = checkedInputsCount(el) > 0
  toggleMailerWarning(canApprove)
  toggleApprovalButton(canApprove)
}

function toggleChecked(el: HTMLInputElement) {
  if (el.getAttribute('aria-checked') === 'true') {
    el.setAttribute('aria-checked', 'false')
  } else {
    el.setAttribute('aria-checked', 'true')
  }
}

// this automates checking of checkboxes when modal loads so that state is retained
function setCheckedState(el: HTMLElement) {
  const checks = el.querySelectorAll<HTMLInputElement>('input[type=checkbox]')

  for (const check of checks) {
    if (check.getAttribute('aria-checked') === 'true') {
      check.checked = true
    } else {
      check.checked = false
    }
  }
}

// set selection state every time modal is opened
on('click', '.js-two-factor-recovery-review-btn', () => {
  const el = document.querySelector<HTMLElement>('#js-2fa-recovery-emails-list')
  if (!el) {
    return
  }
  setCheckedState(el)
  requireMailer(el)
})

// if any checkbox changes, ensure that at least one email is still selected
on('change', '#js-2fa-recovery-emails-list input', () => {
  const el = document.querySelector<HTMLElement>('#js-2fa-recovery-emails-list')!
  requireMailer(el)
})

// Set a checked attribute for state management when modal is closed when box is checked / unchecked
on('click', '#js-2fa-recovery-emails-list input', ({currentTarget}) => {
  toggleChecked(currentTarget as HTMLInputElement)
})
