// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'
import {onKey} from '@github-ui/onfocus'
import {requestSubmit} from '@github-ui/form-utils'

on('click', '.js-minimize-target', function (e) {
  const button = e.currentTarget

  button.classList.toggle('btn')
  button.classList.toggle('btn-link')

  for (const elem of document.getElementsByClassName('js-minimize-toggle')) {
    /* eslint-disable-next-line github/no-d-none */
    elem.classList.toggle('d-none')
  }
})

on('click', '.js-submit-checkbox', function (e) {
  const checkbox = e.currentTarget as HTMLInputElement
  requestSubmit(checkbox.form!)
})

on('click', 'input[type="radio"].js-require-selection', function (e) {
  const radio = e.currentTarget as HTMLInputElement
  if (radio.form) {
    const submitBtn = radio.form.querySelector<HTMLInputElement>('[type=submit]')
    if (submitBtn) {
      submitBtn.classList.remove('disabled')
      submitBtn.disabled = false
    }
  }
})

on('change', '#user_rename_reason', function (e) {
  const reason = e.currentTarget as HTMLSelectElement
  const originalUsername = document.querySelector<HTMLInputElement>('#original_login')!

  const autoRename = reason.value === 'Dormant' && !originalUsername.value.endsWith('-zz')
  const notesRequired =
    reason.value === 'ATO remediation' || reason.value === 'Owner requested' || reason.value === 'Other'

  const username = document.querySelector<HTMLInputElement>('#user_rename_login')!

  if (autoRename) {
    username.value = `${originalUsername.value}-zz`
  } else {
    username.value = originalUsername.value
  }

  const notes = document.querySelector<HTMLInputElement>('#user_rename_notes')!
  notes.toggleAttribute('required', notesRequired)

  const notesContainer = document.querySelector<HTMLElement>('#user_rename_notes_container')!
  notesContainer.classList.toggle('required', notesRequired)
})

on('change', '#access-request-reason', function (e) {
  const reason = e.currentTarget as HTMLSelectElement
  const notesRequired = reason.value === 'Other'

  const notes = document.querySelector<HTMLInputElement>('#impersonate-user-notes')!
  notes.toggleAttribute('required', notesRequired)
  if (!notesRequired) {
    notes.removeAttribute('placeholder')
  }

  const notesContainer = document.querySelector<HTMLElement>('#impersonate-user-notes-container')!
  notesContainer.classList.toggle('required', notesRequired)
})

onKey('keydown', '.js-prevent-keyboard-submit', function (e) {
  // TODO: Refactor to use data-hotkey
  /* eslint eslint-comments/no-use: off */
  /* eslint-disable @github-ui/ui-commands/no-manual-shortcut-logic */
  if (e.key === 'Enter') {
    e.preventDefault()
  }
  /* eslint-enable @github-ui/ui-commands/no-manual-shortcut-logic */
})
