import {fetchPoll} from '@github-ui/fetch-utils'
// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'

// Provides a delayed redirect to a report. Markup:
//
// <div class="js-report-container">
//   <button class="js-report" data-url="/reports/foo">Generate Report</button>
//   <span class="js-report-progress" style="display:none">Generating reportâ€¦</span>
// </div>

on('click', '.js-report', async function (event) {
  event.preventDefault()

  const button = event.target
  if (!(button instanceof HTMLElement)) return
  const container = button.closest<HTMLElement>('.js-report-container')!
  const progress = container.querySelector<HTMLElement>('.js-report-progress')!

  try {
    button.classList.add('disabled')
    /* eslint-disable-next-line github/no-d-none */
    progress.classList.remove('d-none')

    const url = button.getAttribute('data-url')!
    const response = await fetchPoll(url)
    window.location.href = response.headers.get('Location') || ''
  } finally {
    button.classList.remove('disabled')
    /* eslint-disable-next-line github/no-d-none */
    progress.classList.add('d-none')
  }
})
