// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'

on('click', '.js-bulk-privatizations-select-all-public', function (event) {
  const selectAllCheckbox = event.currentTarget as HTMLInputElement
  const form = selectAllCheckbox.closest<HTMLFormElement>('form')

  if (form) {
    const publicRepoCheckboxes = form.querySelectorAll<HTMLInputElement>('.js-bulk-privatizations-public-repo')

    if (publicRepoCheckboxes) {
      for (const checkbox of publicRepoCheckboxes) {
        if (selectAllCheckbox.checked) {
          checkbox.checked = true
        } else {
          checkbox.checked = false
        }
      }
    }
  }
})

on('click', '.js-bulk-privatizations-public-repo', function (event) {
  const repoCheckbox = event.currentTarget as HTMLInputElement
  const form = repoCheckbox.closest<HTMLFormElement>('form')

  if (form) {
    const selectAllCheckbox = form.querySelector<HTMLInputElement>('.js-bulk-privatizations-select-all-public')

    if (selectAllCheckbox && !repoCheckbox.checked) {
      selectAllCheckbox.checked = false
    }
  }
})
