// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'

on('change', '#js-select-all-retired-namespaces', function (event) {
  const ids = []
  const value = (event.currentTarget as HTMLInputElement).checked
  const checkboxes = document.querySelectorAll<HTMLInputElement>('[data-check-all-item]')

  for (const checkbox of checkboxes) {
    checkbox.checked = value

    if (value === true) {
      ids.push(checkbox.value)
    }
  }

  document.querySelector<HTMLInputElement>('#js-retired-namespaces-ids')!.value = `[${ids.join(',')}]`
})

on('change', '[data-check-all-item]', function (event) {
  const retiredIdsElement = document.querySelector<HTMLInputElement>('#js-retired-namespaces-ids')!
  const element = event.currentTarget as HTMLInputElement
  let ids = JSON.parse(retiredIdsElement.value) as number[]

  if (element.checked) {
    ids.push(parseInt((event.currentTarget as HTMLInputElement).value))
  } else {
    ids = ids.filter(id => id !== parseInt(element.value))
  }

  retiredIdsElement.value = `[${ids.join(',')}]`
})
