import {addThrottledInputEventListener} from '../throttled-input'
// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'
// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'

async function updateTotals(baseUrl: string, seats: number, duration: string) {
  const url = new URL(baseUrl, window.location.origin)
  const params = new URLSearchParams(url.search.slice(1))
  params.append('seats', seats.toString())
  params.append('duration', duration)
  url.search = params.toString()

  const response = await fetch(url.toString(), {headers: {Accept: 'application/json'}})
  if (!response.ok) return
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  const data = (await response.json()) as {[key: string]: any}

  const ref = data.selectors
  for (const selector in ref) {
    const text = ref[selector]
    const el = document.querySelector(selector)
    if (el) el.textContent = text
  }

  for (const el of document.querySelectorAll('.js-altered-subscription')) {
    /* eslint-disable-next-line github/no-d-none */
    el.classList.toggle('d-none', data.starting_new_subscription)
  }

  for (const el of document.querySelectorAll('.js-replaced-subscription')) {
    /* eslint-disable-next-line github/no-d-none */
    el.classList.toggle('d-none', !data.starting_new_subscription)
  }
}

function updateDuration(event: Event) {
  const currentTarget = event.currentTarget as HTMLInputElement
  const field = document.querySelector<HTMLInputElement>('.js-total-seats-field')!
  const url = field.getAttribute('data-url')!
  const seats = Math.max(0, parseInt(field.value) || 0)
  const duration = currentTarget.value
  updateTotals(url, seats, duration)
}

function updateSeatCount(el: HTMLInputElement) {
  const seats = Math.max(0, parseInt(el.value) || 0)
  const url = el.getAttribute('data-url')!
  const durationField = document.querySelector('.js-plan-duration:checked')
  if (durationField instanceof HTMLInputElement) {
    updateTotals(url, seats, durationField.value)
  }
}

observe('.js-total-seats-field', {
  constructor: HTMLInputElement,
  initialize(el) {
    updateSeatCount(el)

    addThrottledInputEventListener(el, () => {
      updateSeatCount(el)
    })
  },
})

on('change', 'input.js-plan-duration', updateDuration)
