import {fetchPoll, fetchSafeDocumentFragment} from '@github-ui/fetch-utils'
// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'
import {remoteForm} from '@github/remote-form'

function disableRepositoryButtons(parent: Element) {
  const buttons = parent.querySelectorAll<HTMLButtonElement>('form>button')
  for (const button of buttons) {
    button.disabled = true
  }

  const summaryToggles = parent.querySelectorAll('summary')
  for (const summary of summaryToggles) {
    summary.classList.add('disabled')
  }

  for (const detail of parent.querySelectorAll('details')) {
    detail.removeAttribute('open')
  }

  const checkbox = parent.querySelector<HTMLInputElement>("li > input[type='checkbox']")!
  checkbox.disabled = true

  const button = document.querySelector('.js-purgatory-restore-bulk-button')!
  button.setAttribute('aria-disabled', 'true')
}

function populateStatus(parent: Element, response: {html: DocumentFragment | Element}) {
  const container = parent.querySelector<HTMLElement>('.js-repository-status')!
  container.textContent = ''
  container.append(response.html)
}

function populateError(parent: Element, error: string) {
  const container = parent.querySelector<HTMLElement>('.js-repository-status')!
  container.textContent = ''
  container.append(error)
}

function showStafftoolsLink(parent: Element) {
  const stafftoolsLink = parent.querySelector<HTMLElement>('a.js-stafftools-repository-link')!
  stafftoolsLink.classList.remove('v-hidden')
}

remoteForm('.js-purgatory-detach', async function (form, wants) {
  const parent = form.closest<HTMLElement>('.js-repository-purgatory')!
  disableRepositoryButtons(parent)

  const response = await wants.html()
  populateStatus(parent, response)
})

remoteForm('.js-purgatory-restore', async function (form, wants) {
  const parent = form.closest<HTMLElement>('.js-repository-purgatory')!
  disableRepositoryButtons(parent)

  const response = await wants.html()
  populateStatus(parent, response)
  if (!response.text.includes('errors')) {
    showStafftoolsLink(parent)
  }
})

remoteForm('.js-purgatory-purge', async function (form, wants) {
  const parent = form.closest<HTMLElement>('.js-repository-purgatory')!
  disableRepositoryButtons(parent)

  const response = await wants.html()
  populateStatus(parent, response)
})

// highlight selected private forks and show bulk restore warning
on('change', ".js-private-fork > input[type='checkbox']", event => {
  const checkbox = event.target as HTMLInputElement
  const parent = checkbox.closest<HTMLElement>('.js-repository-purgatory')!
  parent.classList.toggle('flash-warn', checkbox.checked)

  const message = document.querySelector<HTMLElement>('.js-purgatory-restore-bulk-private-fork-message')!
  message.hidden = document.querySelector('.js-private-fork > input:checked') ? false : true
})

on('change', ".js-no-deleted-at > input[type='checkbox']", event => {
  const checkbox = event.target as HTMLInputElement
  const parent = checkbox.closest<HTMLElement>('.js-repository-purgatory')!
  parent.classList.toggle('flash-warn', checkbox.checked)

  const message = document.querySelector<HTMLElement>('.js-purgatory-restore-bulk-deleted-at-message')!
  message.hidden = document.querySelector('.js-no-deleted-at > input:checked') ? false : true
})

on('change', '.js-purgatory-repo-select-checkbox', () => {
  const button = document.querySelector('.js-purgatory-restore-bulk-button')!

  if (anySelectedRepositories()) {
    button.setAttribute('aria-disabled', 'false')
  } else {
    button.setAttribute('aria-disabled', 'true')
  }
})

on('click', '.js-purgatory-restore-bulk-button', event => {
  handleBulkRestoreClick(event)
})

on('mousedown', '.js-purgatory-restore-bulk-button', event => {
  handleBulkRestoreClick(event)
})

function handleBulkRestoreClick(event: MouseEvent) {
  const button = event.currentTarget
  if (button instanceof HTMLElement) {
    if (!anySelectedRepositories() || button.getAttribute('aria-disabled') === 'true') {
      event.preventDefault()
      button.setAttribute('tabindex', '-1')
    } else {
      button.removeAttribute('tabindex')
    }
  }
}

function anySelectedRepositories() {
  return !!document.querySelector('.js-purgatory-repo-select-checkbox:checked')
}

on('click', 'button.js-purgatory-restore-bulk', async event => {
  const button = event.currentTarget as HTMLButtonElement

  // bulk repo restore
  const checkedIdPrefix = 'purgatory-repository-'
  const dialogText = document.querySelector<HTMLElement>('#js-purgatory-restore-bulk-status')!
  const form = document.querySelector<HTMLFormElement>('form.js-purgatory-restore-bulk')!
  button.disabled = true
  const bulkJobs = []

  const startedMessage = dialogText.getAttribute('data-started-message')!
  dialogText.textContent = startedMessage

  // add checked to form
  const selected = document.querySelectorAll<HTMLInputElement>(`[id^='${checkedIdPrefix}'] > input:checked`)
  for (const item of selected) {
    const itemId = item.value
    const input = document.createElement('input')
    input.type = 'hidden'
    input.name = 'ids[]'
    input.value = itemId
    form.appendChild(input)
    const repoContainer = document.querySelector<HTMLElement>(`#${checkedIdPrefix}${itemId}`)!
    disableRepositoryButtons(repoContainer)
    bulkJobs.push(itemId)
  }

  if (bulkJobs.length === 0) {
    const failureMessage = dialogText.getAttribute('data-nonselected-message')!
    dialogText.textContent = failureMessage
    return
  }

  async function onItemQueue(itemId: string) {
    const repoContainer = document.querySelector<HTMLElement>(`#${checkedIdPrefix}${itemId}`)!
    const partialPath = repoContainer.getAttribute('data-restore-partial')!
    const pollStatusPartial = await fetchSafeDocumentFragment(document, partialPath)
    populateStatus(repoContainer, {html: pollStatusPartial.firstElementChild!})
    showStafftoolsLink(repoContainer)
  }

  const url = form.action
  const method = form.method

  try {
    // start the bulk restore job
    const response = await fetch(url, {method, body: new FormData(form), headers: {Accept: 'application/json'}})
    if (!response.ok) throw new Error()

    const data = await response.json()

    // poll each restore JobStatus in turn
    for (const itemId of bulkJobs) {
      const restoreError = data.errors[itemId]
      const repoContainer = document.querySelector<HTMLElement>(`#${checkedIdPrefix}${itemId}`)!

      if (restoreError !== '') {
        populateError(repoContainer, restoreError)
      } else {
        await onItemQueue(itemId)
        const bulkJobStatusPath = repoContainer.getAttribute('data-bulk-job-status')!
        await fetchPoll(bulkJobStatusPath, {headers: {accept: 'application/json'}})
      }
    }
  } catch (error) {
    const failureMessage = dialogText.getAttribute('data-failure-message')!
    dialogText.textContent = failureMessage
    return
  }

  button.closest<HTMLElement>('.js-purgatory-restore-bulk-details')!.removeAttribute('open')
})
